.App {
    max-width: 1200px;
    justify-content: center;
    align-items: center;
    margin: auto;
    display: flex;
  }

form {
    box-sizing: border-box;
    margin-top: 50px;
    background-color: #F6FBF9;
    border-radius: 40px;
    width: 698px;
    height: 698px;
    align-items: center;
    
    
}

.create{
    margin: 68px auto 6px auto;
    font-size: 55.23px;
    width: max-content;
}

p {
    font-size: 23px;
    text-align: center;
    margin: auto;
    max-width: 450px;
    margin-bottom: 53px;
}

input {
    display: flex;
    height: 85px;
    width: 576px;
    font-size: 23px;
    border: 1px solid gray;
    padding-left: 34px;
    margin: auto;
    margin-bottom: 23px;
    border-radius: 15px;

}

button {
    display: flex;
    height: 91px;
    width: 391px;
    margin: auto;
    background-color: #84C7AE;
    color: #FFF;
    margin-bottom: 17px;
    border-radius: 23px;
    border: none;
    font-size: 34px;
    justify-content: center;
    align-items: center;
}

button:hover {
    background-color: #C1E3D6;
}
    
a {
    color: black;
}

